@import "../../global.scss";

.backgroundImage {
    height: 85%;
    width: 100%;
    max-width: 90vw;
    border-radius: 10px;

    background-image: url("../../../public/assets/background.jpg");
    background-size: cover;
    background-position: center;

    .mainContainer {
        background-color: rgba(2, 2, 2, 0.432);
        width: 100%;
        height: 100%;
        border-radius: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    @include mobileTurned{
        height: 70%;
    }
}